
"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { Home, PenTool, FileText, Wallet, History, LogOut } from "lucide-react"
import { useActiveProjects } from "@/store/useActiveProjects"

const sections = [
  {
    title: "ACTIVE PROJECTS",
    items: [], // will be populated dynamically
  },
  {
    title: "MAIN",
    items: [
      { name: "Launch", icon: PenTool, href: "/launch" },
    ],
  },
  {
    title: "CREATE",
    items: [
      { name: "Token Template", icon: FileText, href: "/token-template" },
      { name: "Vanity Address", icon: FileText, href: "/vanity-address" },
    ],
  },
  {
    title: "FINANCE",
    items: [
      { name: "Wallets", icon: Wallet, href: "/wallets" },
      { name: "Transactions", icon: History, href: "/transactions" },
    ],
  },
  {
    title: "SYSTEM",
    items: [
      { name: "Logs", icon: History, href: "/logs" },
      { name: "Log Out", icon: LogOut, href: "/logout" },
    ],
  },
]

export default function Sidebar() {
  const pathname = usePathname()
  const { projects } = useActiveProjects()

  return (
    <div className="w-64 bg-[#0f172a] text-white border-r border-gray-800 flex flex-col">
      <div className="p-4 flex-shrink-0">
        <h1 className="text-2xl font-bold mb-8 text-blue-500">AI Panel</h1>
      </div>
      <nav className="flex-1 overflow-y-auto px-2 space-y-6">
        {/* ACTIVE PROJECTS Section */}
        <div>
          <h2 className="text-xs font-semibold text-gray-400 px-3 mb-2">ACTIVE PROJECTS</h2>
          {projects.length === 0 ? (
            <p className="text-gray-500 px-3 text-sm italic">No active projects</p>
          ) : (
            projects.map((project) => (
              <Link
                key={project.id}
                href={`/project/${project.id}`}
                className={`block px-3 py-2 rounded-md text-sm font-medium transition-colors ${
                  pathname === `/project/${project.id}`
                    ? "bg-blue-600/50 text-blue-500 font-semibold"
                    : "text-gray-400 hover:bg-gray-800/50 hover:text-gray-200"
                }`}
              >
                {project.name}
              </Link>
            ))
          )}
        </div>

        {/* Other Sections */}
        {sections.slice(1).map((section) => (
          <div key={section.title}>
            <h2 className="text-xs font-semibold text-gray-400 px-3 mb-2">{section.title}</h2>
            {section.items.map((item) => {
              const isActive = pathname === item.href
              const Icon = item.icon
              return (
                <Link
                  key={item.name}
                  href={item.href}
                  className={`flex items-center gap-3 px-3 py-2 rounded-lg text-sm font-medium transition-colors ${
                    isActive
                      ? "bg-blue-600/50 text-blue-500 font-semibold"
                      : "text-gray-400 hover:bg-gray-800/50 hover:text-gray-200"
                  }`}
                >
                  <Icon className="w-5 h-5" />
                  <span>{item.name}</span>
                </Link>
              )
            })}
          </div>
        ))}
      </nav>
    </div>
  )
}
